body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  height: 100%;
}

.loading {
  text-align: center;
}
.flag {
  width: 400px;
  display: flex;
  margin: 0 auto;
  padding: 25px;
}

.gamepage {
  display: inline;
  justify-content: center;
}

.form {
  padding: 15px;
  background: #36C740;
  opacity: .8;
}

.inline {
  display: inline;
}

.submitbutton {
  display: flex;
  padding: 5px;
  background: white;
}
